/*---  Accordation  ---*/

.accordion {
  > .card {
    overflow: hidden;

    &:not(:first-of-type) {
      .card-header:first-child {
        border-radius: 0;
      }

      &:not(:last-of-type) {
        border-bottom: 0;
        border-radius: 0;
      }
    }

    &:first-of-type {
      border-bottom: 0;
      border-bottom-right-radius: 0;
      border-bottom-left-radius: 0;
    }

    &:last-of-type {
      border-top-left-radius: 0;
      border-top-right-radius: 0;
    }

    .card-header {
      margin-bottom: -1px;
    }
  }

  .card {
    border-width: 0;

    + .card {
      margin-top: 1px;
    }
  }

  .card-header {
    padding: 0;
    background-color: transparent;
    font-weight: 500;

    a {
      display: block;
      padding: 14px 20px;
      color: $color;
      position: relative;
      font-weight: 500;
      font-size: 14px;
      background-color: #eef0fb;

      &:hover, &:focus {
        color: $color;
      }

      &.collapsed {
        color: #596882;
        background-color: #e2e4ef;
        border-bottom-color: transparent;

        &:hover, &:focus {
          color: $color;
          background-color:$border;
        }
      }
    }
  }

  .card-body {
    padding: 15px;
    background-color: $white;
    transition: none;
    border: 1px solid$border;
  }
}

.accordion-indigo {
  .card {
    border-color: $primary;
  }

  .card-header a {
    color: $primary;

    &:hover, &:focus {
      color: $primary;
    }

    &.collapsed {
      background-color: $primary;
      color: rgba(255, 255, 255, 0.8);
      font-weight: 500;
      letter-spacing: -.1px;

      &:hover, &:focus {
        background-color: $primary;
        color: $white;
      }
    }
  }
}

.accordion-blue {
  .card {
    border-color: $primary;
  }

  .card-header a {
    color: $primary;

    &:hover, &:focus {
      color: $primary;
    }

    &.collapsed {
      background-color: $primary;
      color: rgba(255, 255, 255, 0.8);
      font-weight: 500;
      letter-spacing: -.1px;

      &:hover, &:focus {
        background-color: $primary;
        color: $white;
      }
    }
  }
}

.accordion-dark {
  .card {
    border-color: $color;
  }

  .card-header a {
    color: $color;

    &:hover, &:focus {
      color: $color;
    }

    &.collapsed {
      background-color: $color;
      color: rgba(255, 255, 255, 0.8);
      font-weight: 500;
