.billed-from {
  margin-top: 25px;
  margin-bottom: 25px;

  h6 {
    color: $color;
    font-size: 14px;
    font-weight: 700;
  }

  p {
    font-size: 13px;
    margin-bottom: 0;
  }
}

.billed-to {
  h6 {
    color: $color;
    font-size: 14px;
    font-weight: 700;
  }

  p {
    font-size: 13px;
    margin-bottom: 0;
  }
}

.main-traffic-detail-item {
  > div:first-child {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 12px;
    margin-bottom: 7px;

    > span {
      &:first-child {
        color: $muted;
      }

      &:last-child {
        font-size: 11px;
        font-weight: 700;
        color: $color;

        span {
          color: $muted;
          font-weight: 400;
        }
      }
    }
  }

  + .main-traffic-detail-item {
    margin-top: 18px;
  }
}

/*  ----- 7.11 Error   ----- */

.main-error-wrapper {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 20px;
  text-align: center;

  h1 {
    line-height: 1;
    font-size: 8rem;
    font-weight: 700;
    color: $color;
  }

  h2 {
    font-weight: 500;
    color: #3b4863;
    letter-spacing: -.5px;
    margin-bottom: 15px;
  }

  h6 {
    margin-bottom: 40px;
    font-size: 14px;
  }

  .btn, .sp-container button {
    border-width: 2px;
    font-weight: 400;
  }
}

.sp-container .main-error-wrapper button {
  border-width: 2px;
  font-weight: 400;
}

.main-star-group {
  display: flex;
  align-items: center;

  span:last-child {
    display: block;
    font-weight: 500;
    font-size: 11px;
    margin-left: 5px;
    color: $muted;
  }
}

.main-star-item {
  color: $primary;
  font-size: 14px;
  position: relative;

  + .main-star-item {
    margin-left: 3px;
  }
}

.overflow-y-auto {
  overflow-y: auto;
}

.img-fit-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-img-user {
    display: block;
    position: relative;
    width: 36px;
    height: 36px;
    border-radius: 100%;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 100%;
    }
  
  .error-bg {
    background: url(../img/pngs/bg.png);
    background-position: center;
    background-size: cover;
    }
}

.main-signin-wrapper {
    flex: 1;
    display: flex;
    justify-content: center;
  }

/*---- IE css ---*/
*::-ms-backdrop, .b-img {
  height: 197px !important;
}
@media print {
    * {
      text-shadow: none !important;
      box-shadow: none !important;
  
      &::before, &::after {
        text-shadow: none !important;
        box-shadow: none !important;
      }
    }
  
    a:not(.btn) {
      text-decoration: underline;
    }
  
    abbr[title]::after {
      content: " (" attr(title) ")";
    }
  
    pre {
      white-space: pre-wrap !important;
      border: 1px solid $muted;
      page-break-inside: avoid;
    }
  
    blockquote {
      border: 1px solid $muted;
      page-break-inside: avoid;
    }
  
    thead {
      display: table-header-group;
    }
  
    tr, img {
      page-break-inside: avoid;
    }
  
    p, h2, h3 {
      orphans: 3;
      widows: 3;
    }
  
    h2, h3 {
      page-break-after: avoid;
    }
  
    @page {
      size: a3;
    }
  
    body, .container {
      min-width: 992px !important;
    }
}
  @media (prefers-reduced-motion: reduce) {
    .fade, .collapsing, .ps > .ps__rail-y {
      transition: none;
    }
  
    .main-accordion {
      .accordion-item, .accordion-title {
        transition: none;
      }
    }
  
    .main-toggle span {
      transition: none;
    }
}
