    }

    .main-header {
        position: fixed;
        box-shadow: none;
        border-bottom: 1px solid #e1e6f1;
        border-top: 0;
    }

    .header-settings .nav-link.icon {
        padding-right: 0;
    }

    .main-logo .header-brand-img {
